@import "_fonts";
@import "_colors";
@import "animations";
@import "_transitions";

html,
body {
  margin: 0 auto;
  padding: 0;
  font-family: $open-sans;
  color: $white;
  position: relative;
}

body {
  &::-webkit-scrollbar {
    width: 12px;

    &-thumb {
      background-color: $lucky-point;
    }

    &-track {
      background-color: $white;
    }
  }
}

*,
*:after,
*:before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

a {
  text-decoration: none !important;
  -webkit-transition: $transition1-all;
  transition: $transition1-all;
  color: $white;

  &:hover {
    color: $white;
  }
}

img {
  width: 100%;
  height: auto;
}

h2 {
  text-align: center;
  letter-spacing: 3px;
  margin-bottom: 50px;
  text-transform: uppercase;
  font-size: 40px;
  font-family: $poppins;
}

:focus {
  outline: none !important;
  -webkit-box-shadow: none !important;
  -moz-box-shadow: none !important;
  box-shadow: none !important;
}

/* Loader */
.loader {
  position: fixed;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: $lucky-point;
  z-index: 9;

  svg {
    -webkit-animation: spinner 4s cubic-bezier(0.5, 0.5, 0.5, 0.5) infinite;
    animation: spinner 4s cubic-bezier(0.5, 0.5, 0.5, 0.5) infinite;
  }
}

/* Loader end */

/* App */
#app {
  padding-top: 90px;
}

/* Fixed top */
.fixed-top {
  background-color: $lucky-point;
  height: 90px;
  display: flex;
  align-items: center;
  -webkit-transition: $transition1-all;
  transition: $transition1-all;
  z-index: 6;

  &.active {
    height: 100px;

    .logo {
      .nav-link {
        svg {
          width: 130px;
        }
      }
    }
  }
}

/* Push container */
.push-container {
  overflow: hidden;
}

/* Push container end */

/* Push app */
.push-app {
  position: relative;
  -webkit-transition: $transition1-all;
  transition: $transition1-all;
  left: 0;
  z-index: 1;

  &.active {
    transform: translate3d(80%, 0, 0);
  }
}

/* Push app end */

.background {
  position: fixed;
  top: 50px;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  -webkit-transition: $transition1-all;
  transition: $transition1-all;

  &.active {
    transform: translate3d(80%, 0, 0);
  }

  img {
    height: 100vh;
    padding-bottom: 50px;
  }
}

.wrapper {
  padding-top: 110px;
}

.lg-outer .lg-item {
  background-size: 50px;
}

/* Fixed top end */

/* Logo */
.logo {
  .nav-link {
    padding: 0;

    svg {
      fill: $white;
      -webkit-transition: $transition1-all;
      transition: $transition1-all;
      fill: $silver;

      &:hover {
        fill: $white;
      }
    }
  }
}

/* Logo end */

/* Nav */
.nav {
  .navbar-nav {
    margin: 0;
    padding: 0;

    .nav-item {
      list-style: none;
      margin: 0;
    }

    .nav-link {
      display: inline-block;
      color: $silver;
      position: relative;
      margin: 25px;
      margin-bottom: 0;
      padding: 0;
      padding-bottom: 25px;
      letter-spacing: 2px;
      font-size: 14px;

      &.active {
        color: $white;

        svg {
          visibility: visible;
          opacity: 1;

          .cls-1,
          .cls-2,
          .cls-3 {
            fill: $tango;
          }
        }
      }

      &:hover {
        &:not(.active) {
          color: $white;

          svg {
            fill: $white;
            visibility: visible;
            opacity: 1;
          }
        }
      }

      svg {
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translate(-50%, 0);
        visibility: hidden;
        opacity: 0;
        -webkit-transition: opacity $transition1;
        transition: opacity $transition1;

        .cls-1,
        .cls-2,
        .cls-3 {
          fill: $white;
        }

        .cls-4 {
          fill: #333;
        }

        .cls-5 {
          fill: #4d4d4d;
        }

        .cls-6 {
          fill: #1f1f1f;
        }
      }
    }
  }
}

/* Nav end */

/* Header */
header {
  height: calc(100vh - 90px);
  position: relative;

  /* Carousel */
  .carousel {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    &::after {
      content: " ";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.4);
      z-index: 1;
    }

    .carousel-inner {
      height: 100%;

      .carousel-item {
        height: 100%;

        img {
          height: 100%;
        }
      }
    }
  }

  /* Carousel end */

  /* Welcome */
  .welcome {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    width: 100%;
    text-align: center;
    font-size: 70px;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-family: $righteous;
  }

  /* Welcome end */

  .read-more {
    position: absolute;
    bottom: 50px;
    left: 50%;
    transform: translate(-50%, 0);
    z-index: 1;

    .button {
      display: inline-block;
      position: relative;
      background-color: $lucky-point;
      color: $white;
      padding: 2px;
      min-width: 150px;
      height: 44px;
      -ms-border-radius: 40px;
      border-radius: 40px;
      border: none;

      &:hover {
        span {
          background-color: transparent;
          color: $white;
        }
      }

      span {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        -ms-border-radius: 40px;
        border-radius: 40px;
        -webkit-transition: $transition-3;
        transition: $transition-3;
        background-color: $black;
        padding: 12px 20px;
        text-transform: uppercase;
        font-weight: 700;
      }
    }
  }
}

/* Header end */

/* Main */

main {
  &::before {
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url('../img/background/background.jpg') top center no-repeat fixed;
    background-size: cover;
    z-index: -2;
  }

  &::after {
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: -1;
  }
}

/* About club */
#about-club {
  .box {
    border-radius: 15px;
    background-color: rgba($black, 0.8);
    padding: 30px;
    -webkit-transition: $transition1-all;
    transition: $transition1-all;

    &:hover {
      background-color: rgba($black, 1);
    }

    p {
      line-height: 40px;
      text-align: justify;
      font-size: 18px;
      margin-bottom: 0;
      font-weight: 300;
    }
  }
}

/* About club end */

/* Selections */
#selections {
  .item {
    margin-bottom: 30px;

    &:nth-last-child(-n+3) {
      margin-bottom: 0;
    }
  }

  .box {
    .item-img {
      margin-bottom: 10px;
      border-radius: 6px;
      opacity: 0.8;
      -webkit-transition: $transition1-all;
      transition: $transition1-all;
      overflow: hidden;

      &:hover {
        opacity: 1;
        transform: translateY(-5px);
        -webkit-transform: translateY(-5px);
        -webkit-box-shadow: 0px 0px 15px -5px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 0px 0px 15px -5px rgba(0, 0, 0, 0.75);
        box-shadow: 0px 0px 15px -5px rgba(0, 0, 0, 0.75);

        svg {
          visibility: visible;
          opacity: 1;
        }
      }

      img {
        height: 220px;
      }

      svg {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        visibility: hidden;
        opacity: 0;
        -webkit-transition: $transition1-all;
        transition: $transition1-all;
      }
    }

    h4 {
      font-size: 18px;
      text-align: center;
      margin-bottom: 0;
      font-style: italic;
    }
  }
}

/* Selections end */

/* Coaches */
#coaches {
  .item {
    text-align: center;
  }

  .box {
    .item-img {
      margin: 0 auto;
      border-radius: 50%;
      overflow: hidden;
      width: 160px;
      height: 160px;
      margin-bottom: 15px;
      border: 3px solid $white;
      opacity: 0.8;
      -webkit-transition: $transition1-all;
      transition: $transition1-all;
      cursor: pointer;

      &:hover {
        opacity: 1;
      }
    }

    .name {
      font-size: 18px;
      margin-bottom: 0;
      font-weight: 700;
    }

    .title {
      font-size: 14px;
      margin-bottom: 0;
      font-style: italic;
    }
  }
}

/* Coaches end */

/* Gallery */
#gallery {
  .item {
    margin-bottom: 30px;

    &:nth-last-child(-n+4) {
      margin-bottom: 0;
    }

    .item-img {
      border-radius: 6px;
      opacity: 0.8;
      -webkit-transition: $transition1-all;
      transition: $transition1-all;
      overflow: hidden;

      &:hover {
        opacity: 1;
        transform: translateY(-5px);
        -webkit-transform: translateY(-5px);
        -webkit-box-shadow: 0px 0px 15px -5px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 0px 0px 15px -5px rgba(0, 0, 0, 0.75);
        box-shadow: 0px 0px 15px -5px rgba(0, 0, 0, 0.75);

        svg {
          visibility: visible;
          opacity: 1;
        }
      }

      img {
        height: 150px;
      }

      svg {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        visibility: hidden;
        opacity: 0;
        -webkit-transition: $transition1-all;
        transition: $transition1-all;
      }
    }
  }
}

/* Gallery end */

/* Contact */
#contact {
  .item {
    margin-bottom: 30px;

    &:nth-last-child(-n+3) {
      margin-bottom: 0;
    }
  }

  .key {
    margin-bottom: 0;
    font-weight: 700;
  }

  .value {
    margin-bottom: 0;

    a {
      border-bottom: 1px solid transparent;

      &:hover {
        border-color: $white;
      }
    }
  }

  .map {
    margin-top: 50px;

    iframe {
      width: 100%;
      height: 350px;
      display: block;
    }
  }
}

/* Contact end */

/* Footer */
footer {
  background-color: $black;
  border-top: 4px solid $lucky-point;

  .wrapper {
    padding: 25px 0;
  }

  .copyright {
    font-family: $open-sans;
    color: $silver;
    font-size: 14px;

    a {
      border-bottom: 1px solid transparent;

      &:hover {
        border-color: $white;
      }
    }
  }
}

/* Footer end */

/* Back to top */
.back-to-top {
  position: fixed;
  right: 15px;
  bottom: -44px;
  width: 44px;
  height: 44px;
  -webkit-transition: $transition1-all;
  transition: $transition1-all;
  background-color: $lucky-point;
  z-index: 5;

  &.show {
    bottom: 15px;
  }

  &:hover {
    svg {
      fill: $white;
    }
  }

  svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transition: $transition2-all;
    transition: $transition2-all;
    fill: $silver;
  }
}

/* Back to top end */

/* Media queries */

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
  #selections {
    .item {

      &:nth-last-child(2),
      &:nth-last-child(3) {
        margin-bottom: 30px;
      }
    }
  }

  #coaches {
    .item {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  #gallery {
    .item {

      &:nth-last-child(3),
      &:nth-last-child(4) {
        margin-bottom: 30px;
      }

      .item-img {
        img {
          height: 110px;
        }
      }
    }
  }
}

// Extra small devices (portrait phones, less than 576px) end

// Small devices (landscape phones, less than 768px)
@media (max-width: 767.98px) {
  header {
    .read-more {
      .button {
        min-width: 130px;
        height: 40px;

        span {
          font-size: 14px;
        }
      }
    }
  }
}

// Small devices (landscape phones, less than 768px) end

// Medium devices (tablets, less than 992px)
@media (max-width: 991.98px) {

  .loader {
    svg {
      width: 150px;
    }
  }

  /* Side-nav */

  .side-nav {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    width: 80%;
    height: 100vh;
    overflow: auto;
    background-color: $lucky-point;
    z-index: 1;

    &.active {
      visibility: visible;
      opacity: 1;
    }

    .wrapper {
      padding: 50px 0 15px;

      .logo {
        position: absolute;
        left: 50%;
        top: 10px;
        transform: translate(-50%, 0);

        svg {
          width: 75px;
          fill: $white;
        }
      }
    }

    .nav {
      display: block;

      .navbar-nav {
        display: block;

        .nav-link {
          margin: 0;
          padding: 10px;
          padding-left: 35px;
          position: relative;
          font-size: 18px;

          svg {
            bottom: 50%;
            left: 5px;
            transform: translate(0, 50%);
          }
        }
      }
    }
  }

  /* Side-nav end */

  #app {
    padding-top: 50px;
  }

  /* Top-bar */
  .top-bar {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    height: 50px;
    left: 0;
    z-index: 5;
    background-color: $lucky-point;
    -webkit-transition: $transition1-all;
    transition: $transition1-all;

    &.active {
      transform: translate3d(80%, 0, 0);
    }

    .menu {
      position: absolute;
      top: 50%;
      left: 15px;
      transform: translate(0, -50%);
      width: 40px;
      height: 24px;

      .hamburger {
        width: 40px;
        height: 24px;
        padding: 0;

        &:hover {
          opacity: 1;
        }

        .hamburger-box {

          .hamburger-inner,
          .hamburger-inner::before,
          .hamburger-inner::after {
            background-color: $white;
            -webkit-transition: $transition2-all;
            transition: $transition2-all;
          }
        }

        &.is-active {
          .hamburger-inner {
            background-color: transparent;
          }
        }
      }
    }
  }

  /* Top-bar end */

  .wrapper {
    padding-top: 50px;
  }

  header {
    height: calc(100vh - 50px);

    .welcome {
      font-size: 40px;
    }

    .read-more {
      bottom: 30px;
    }
  }

  #about-club {
    .box {
      padding: 20px;

      p {
        text-align: left;
        line-height: 35px;
        font-size: 16px;
      }
    }
  }

  #coaches {
    .item {
      margin-bottom: 50px;
    }
  }

  #contact {
    .item {

      &:nth-last-child(2),
      &:nth-last-child(3) {
        margin-bottom: 30px;
      }
    }

    .map {
      iframe {
        height: 200px;
      }
    }
  }

  /* Close-nav */
  .close-nav {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 0;
    z-index: 1;

    &.active {
      width: 100%;
      -webkit-animation: 0.3s cubic-bezier(0.50, 0.50, 0.50, 0.50) activeBckg forwards;
      animation: 0.3s cubic-bezier(0.50, 0.50, 0.50, 0.50) activeBckg forwards;

      @keyframes activeBckg {
        0% {
          background-color: rgba(0, 0, 0, 0);
        }

        10% {
          background-color: rgba(0, 0, 0, 0.05);
        }

        20% {
          background-color: rgba(0, 0, 0, 0.1);
        }

        30% {
          background-color: rgba(0, 0, 0, 0.15);
        }

        40% {
          background-color: rgba(0, 0, 0, 0.2);
        }

        50% {
          background-color: rgba(0, 0, 0, 0.25);
        }

        60% {
          background-color: rgba(0, 0, 0, 0.3);
        }

        70% {
          background-color: rgba(0, 0, 0, 0.35);
        }

        80% {
          background-color: rgba(0, 0, 0, 0.4);
        }

        90% {
          background-color: rgba(0, 0, 0, 0.45);
        }

        100% {
          background-color: rgba(0, 0, 0, 0.5);
        }
      }
    }

    &.no-active {
      -webkit-animation: 0.3s cubic-bezier(0.50, 0.50, 0.50, 0.50) no-activeBckg forwards;
      animation: 0.3s cubic-bezier(0.50, 0.50, 0.50, 0.50) no-activeBckg forwards;

      @keyframes no-activeBckg {
        0% {
          background-color: rgba(0, 0, 0, 0.5);
          width: 100%;
        }

        10% {
          background-color: rgba(0, 0, 0, 0.45);
          width: 100%;
        }

        20% {
          background-color: rgba(0, 0, 0, 0.4);
          width: 100%;
        }

        30% {
          background-color: rgba(0, 0, 0, 0.35);
          width: 100%;
        }

        40% {
          background-color: rgba(0, 0, 0, 0.3);
          width: 100%;
        }

        50% {
          background-color: rgba(0, 0, 0, 0.25);
          width: 100%;
        }

        60% {
          background-color: rgba(0, 0, 0, 0.2);
          width: 100%;
        }

        70% {
          background-color: rgba(0, 0, 0, 0.15);
          width: 100%;
        }

        80% {
          background-color: rgba(0, 0, 0, 0.1);
          width: 100%;
        }

        90% {
          background-color: rgba(0, 0, 0, 0.05);
          width: 100%;
        }

        99% {
          background-color: rgba(0, 0, 0, 0.01);
          width: 0.01%;
        }

        100% {
          background-color: rgba(0, 0, 0, 0);
          width: 0;
        }
      }
    }
  }

  /* Close-nav end */

  .back-to-top {
    &.active {
      right: -44px;
    }
  }
}

// Medium devices (tablets, less than 992px) end

// Large devices (desktops, less than 1200px)
@media (max-width: 1199.98px) {
  main {
    &::before {
      display: none;
    }
  }

  #selections {
    .box {
      .item-img {
        opacity: 1;
      }
    }
  }

  #coaches {
    .item {
      .item-img {
        opacity: 1;
      }
    }
  }

  #gallery {
    .item {
      .item-img {
        opacity: 1;
      }
    }
  }
}

// Large devices (desktops, less than 1200px) end

/* Media queries end */

/* Media queries for pictures */

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
  header {
    .carousel {
      .carousel-inner {
        .carousel-item {
          img {
            width: 200% !important;
            margin-left: -50%;
          }
        }
      }
    }
  }
}

// Extra small devices (portrait phones, less than 576px) and (orientation: landscape)
@media (max-width: 575.98px) and (orientation: landscape) {
  header {
    .carousel {
      .carousel-inner {
        .carousel-item {
          img {
            width: 100% !important;
            margin-left: 0;
            height: 150%;
          }
        }
      }
    }
  }

  #selections {
    .box {
      .item-img {
        img {
          height: 80vh;
        }
      }
    }
  }
}

// Extra small devices (portrait phones, less than 576px) and (orientation: landscape) end

// Small devices (landscape phones, 576px and up)
@media (min-width: 576px) and (max-width: 767.98px) {
  .side-nav {
    width: 50%;
  }

  header {
    .carousel {
      .carousel-inner {
        .carousel-item {
          img {
            width: 100% !important;
            height: 150%;
          }
        }
      }
    }
  }

  #selections {
    .item {
      &:nth-last-child(3) {
        margin-bottom: 30px;
      }
    }
  }
}

// Small devices (landscape phones, 576px and up) end

// Small devices (landscape phones, 576px and up) and (orientation: lanscape)
@media (min-width: 576px) and (max-width: 767.98px) and (orientation: landscape) {
  .background {
    img {
      height: 200vh;
    }
  }

  .background,
  .top-bar,
  .push-app {
    &.active {
      transform: translate3d(50%, 0, 0);
    }
  }

  #selections {
    .item {

      &:nth-last-child(2) {
        margin-bottom: 0;
      }
    }

    .box {
      .item-img {
        img {
          height: 50vh;
        }
      }
    }
  }

  #coaches {
    .item {
      &:nth-last-child(-n+2) {
        margin-bottom: 0;
      }
    }
  }

  #gallery {
    .item {

      &:nth-last-child(4) {
        margin-bottom: 30px;
      }

      .item-img {
        img {
          height: 110px;
        }
      }
    }
  }
}

// Small devices (landscape phones, 576px and up) and (orientation: lanscape) end

// Medium devices (tablets, 768px and up)
@media (min-width: 768px) and (max-width: 991.98px) {
  .side-nav {
    width: 50%;
  }

  .background,
  .top-bar,
  .push-app {
    &.active {
      transform: translate3d(50%, 0, 0);
    }
  }

  header {
    .carousel {
      .carousel-inner {
        .carousel-item {
          img {
            width: 200% !important;
            margin-left: -50%;
          }
        }
      }
    }

    .welcome {
      font-size: 50px;
    }
  }

  #selections {
    .item {

      &:nth-last-child(-n+3) {
        margin-bottom: 0;
      }
    }

    .box {
      .item-img {
        img {
          height: 15vh;
        }
      }
    }
  }

  #coaches {
    .item {
      &:nth-last-child(-n+2) {
        margin-bottom: 0;
      }
    }
  }

  #gallery {
    .item {
      .item-img {
        img {
          height: 110px;
        }
      }
    }
  }
}

